/* 구글 로그인 버튼: 브랜딩 가이드라인 준수 규격 */
.google-login-button {
  display: flex;
  align-items: center;
  background-color: #4285f4;
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s;
  padding: 0 16px 0 2px;
  width: fit-content;
  height: 40px;
}

.google-login-button:hover {
  background-color: #357ae8;
}

.bc-logo {
  background-color: #ffffff;
  border-radius: 2px;
  padding: 3px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 12px;
  width: 35px;
  height: 35px;
}

.google-logo {
  width: 18px;
  height: 18px;
}

.login-text {
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  color: white;
}
